<template>
  <div class="page chat" v-if="ready">
    <topheader class="topheader">
     <template v-slot:left>
        <backButton action="chats"/>
      </template>
      <template v-slot:info>
        <span class="nameline">{{ $t("caption.publicRoomTitle") }}</span>
      </template>
    </topheader>
    <div class="publicPreviewContent" v-if="room.length > 0">
      <div class="name">
        <span>{{ room[0].name }}</span>
      </div>
      <div class="topic" v-if="room[0].topic">
        <span>{{ room[0].topic.replace(/_/g, ' ') }}</span>
      </div>
      <div class="membersCount">
        <i class="fas fa-users"></i>
        <span>{{ room[0].num_joined_members }}</span>
      </div>
      <div class="actions">
<!--        <button class="button small black rounded">cancel</button>-->
        <button class="button small rounded" @click="joinRoom()">{{ $t("button.join") }} </button>
      </div>
    </div>
  </div>
</template>

<style scoped lang="sass">
.chat.mobile
  /deep/ #maincontent
    .headerSpacerWrapper
      bottom: 0
      overflow: visible

    .headerSpacerWrapperOvf
      overflow: visible

</style>

<script>

import {mapState} from 'vuex';
import topheader from '@/components/assets/topheader/index.vue';
import chat from "@/views/chat";

export default {
  name: 'publicPreview',
  data: function () {
    return {
      ready: false,
      room: {}
    }
  },
  components: {
    topheader,
  },
  computed: mapState({

    active: state => state.active,
    pocketnet: state => state.pocketnet,
    minimized: state => state.minimized,
    pocketteammessages: state => state.pocketteammessages,
  }),
  methods: {
    joinRoom(){
      this.core.mtrx.client.joinRoom(this.room[0].room_id).then(r=>{
        this.$router.push('/chat?id=' + this.room[0].room_id)
      })
    }
  },
  mounted() {
    this.core.mtrx.wait().then(r => {
      this.core.mtrx.client.publicRooms().then(r => {
        this.ready = true
        if(this.$route.query.id[0] === '!'){
          console.log(this.$route.query.id, "test", r['chunk'].filter(i => i.room_id === this.$route.query.id))
          return this.room = r['chunk'].filter(i => i.room_id === this.$route.query.id)
        }else {
          return this.room = r['chunk'].filter(i => i.name === this.$route.query.id.replace(/_/g, ' ', ))

        }
      })

    })
  }

}
</script>
<style lang="sass" scoped>
.publicPreviewContent

  .name
    margin: 20px 0
    text-align: center

    span
      font-weight: bold
      font-size: 1.1em

  .topic
    text-align: center
    padding: 0 50px

    span
      font-size: 1em
      width: 100%
      display: block
      word-wrap: break-word

  .membersCount
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center

    span
      display: block

    i
      font-size: 1.2em
      color: $black-color-text-main
      margin: 20px 0 10px 0

  .actions
    display: flex
    justify-content: center
    align-items: center
    margin: 50px auto

</style>
